import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);


export default new Router({
  // mode:"history",
  routes: [
    {path:"/",redirect:"/home",},
    {path:"/home",component:()=>import("../components/Home.vue"),meta:{keepAlive:true,title:"首页"}},
    //路径参数 必须有但是可以随机
    {path:"/product",component:()=>import("../components/product.vue"),meta:{title:"产品信息"}},
    {path:"/source",component:()=>import("../components/Detail.vue"),name:"detail",meta:{title:"源头信息"}},
    {path:"/generate",component:()=>import("../components/Add.vue"),meta:{title:"生产信息"}},
    {path:"/company",component:()=>import("../components/Company.vue"),meta:{title:"企业信息"}},
    {path:"/trace",component:()=>import("../components/Trace.vue"),meta:{title:"物流信息"}},
    {path:"/collect",component:()=>import("../components/Quality.vue"),meta:{title:"资质证书"}},
    {path:"*",redirect:"/home"},

  ]
})



