<template>
  <div id="app">
    <div>234356</div>
    <!--需要缓存的-->
    <transition name="fadeIn">
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
    </transition>
    <!--正常的访问走下面-->
   <transition name="fadeIn">
     <router-view v-if="!$route.meta.keepAlive"></router-view>
   </transition>
    <!--<Tab></Tab>-->
  </div>
</template>

<script>
export default {
  name: 'app',
  components:{
  }
}
</script>

<style lang="less">
  *{
    margin: 0;
    padding: 0;

  }
  ul,li{
    list-style: none;
  }
  a{
    text-decoration: none;
  }
  input,button{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
  #app{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: url("https://sunflowerswjj.github.io/images/bg.png");
    background-size: 100% 100%;
  }
  .content{
    position: fixed;width: 100%;top: 40px;
    bottom: 50px;overflow: auto;
    h1{
      margin-top: 100px;
      text-align: center;
      font-size: 20px;
      color: gold;
    }
    .Info{
      padding: 0 30px;
      .top,.center,footer{
        margin-bottom: 20px;
        border-radius: 10px;
        /*box-shadow: 5px 5px 5px 0px #cddaee;*/
        background-color: #f2fcfe;
        p{
          padding: 5px 0;
          font-size: 10px;
          text-align: center;
        }
      }
      .footer{
        .icon{
          position: relative;
          img{
            width: 100%;
            height: 100%;
            border-radius: 8px;
          }
          .tab{
            width: 100%;
            position: absolute;
            height: 150px;
            bottom: 0;
            left: 0;
            float: left;
            a{
              float: left;
              /*border: 1px solid red;*/
              margin-left: 10px;
              margin-bottom: 4px;
              span{
                display: inline-block;
                width: 65px;
                height: 65px;
              }

            }

          }
        }

      }
    }

  }
  .fadeIn-enter{
    opacity: 0;
  }
  .fadeIn-enter-active{
    transition: all 0.3s linear;
  }
  .fadeIn-leave-active{
    transition: all 0.3s linear;
    opacity: 0;
  }
</style>



